import React from "react";

const TripDetailsTabs = ({ trip, activeTab, setActiveTab }) => {
  const renderTabContent = () => {
    switch (activeTab) {
      case "discounts":
        return (
          <div>
            <h3 className="font-semibold text-lg mb-2">Giảm giá</h3>
            <ul>
              <li>Giảm 50% tối đa 250k</li>
              <li>Giảm 20% cho vé đầu tiên</li>
            </ul>
          </div>
        );
      case "policy":
        return (
          <div>
            <h3 className="font-semibold text-lg mb-2">Chính sách</h3>
            <p>Hoàn tiền 100% nếu huỷ trước 24 giờ.</p>
            <p>Hỗ trợ đổi vé miễn phí.</p>
          </div>
        );
      case "images":
        return (
          <div className="grid grid-cols-2 gap-4">
            {trip.busType.images &&
              trip.busType.images.map((image, index) => (
                <img
                  key={index}
                  src={image}
                  alt={`Bus image ${index + 1}`}
                  className="w-full h-32 object-cover rounded-lg"
                />
              ))}
          </div>
        );
      default:
        return null;
    }
  };

  return (
    <div>
      <div className="flex border-b mb-4">
        <button
          className={`px-4 py-2 ${
            activeTab === "discounts" ? "border-b-2 border-blue-500" : ""
          }`}
          onClick={() => setActiveTab("discounts")}
        >
          Giảm giá
        </button>
        <button
          className={`px-4 py-2 ${
            activeTab === "policy" ? "border-b-2 border-blue-500" : ""
          }`}
          onClick={() => setActiveTab("policy")}
        >
          Chính sách
        </button>
        <button
          className={`px-4 py-2 ${
            activeTab === "images" ? "border-b-2 border-blue-500" : ""
          }`}
          onClick={() => setActiveTab("images")}
        >
          Hình ảnh
        </button>
      </div>
      {renderTabContent()}
    </div>
  );
};

export default TripDetailsTabs;
